.bb {
    background-color: #2766cc;
    padding: 17px;
    display: -webkit-flex;
    display: -webkit-box;
    display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
    justify-content: space-around;
    align-items: center;
    color: white;
}
.p {
    color: white;
    text-align: center;
    width: 0%;
}
.first, .second, .third {
    width: 30px;
    text-align: center;
}


@media (min-width: 700px) {
    .bb {
        width: 100%;
    }
    .bb {
        margin-top: 40px;
        display: -webkit-flex;
        display: -webkit-box;
        display: flex;
            -webkit-box-pack: justify;
            -ms-flex-pack: justify;
        justify-content: space-evenly;
        align-items: center;
        color: white;

    }
    .car, .mark, .supp {
        width: 70%;
    }
    .first, .second, .third {
        width: 70px;
        text-align: center;
    }

}
