.calculation {
    margin-top: 40px;
}
.cal {
    display: -webkit-flex;
    display: -webkit-box;
    display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
    justify-content: space-between;
}
.tot {
    display: -webkit-flex;
    display: -webkit-box;
    display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
    justify-content: space-between;
}
.check, .chick{
    background-color: yellow;
    text-align: center;
    margin-bottom: 100px;
    margin-top: 10px;
    padding: 10px;
}
.chick {
    display: none;
}
.nav-links {
    text-decoration: none;
}
@media (min-width: 700px) {
    .chick{
        display: block;
    }
    .check {
        display: none;
    }
    .cas {
        display: none;
    }
}