nav {
  display: -webkit-flex;
  display: -webkit-box;
  display: flex;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
  justify-content: space-evenly;
  align-items: center;
  border: none;
  position: fixed;
  bottom: 0;
  background-color: white;

  width: 100%;
}
.activate {
  color: #2766cc;
  width: 100%;
}
.deactivate {
  color: #b3b3b3;
}
.lines{
 padding: 0.5em;
  position: relative;
  text-align: center;
}
.i {
  font-size: 30px;
  width: 40px;

}
.pp{
  text-align: center;
}
.numberings {
  background-color: #0ac6fc;
  color: white;
  text-align: center;
  padding-top: 5px;
  border-radius: 50px;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 5px;
  margin-left: 25px;

}
@media (min-width: 700px) {
  nav{
     display: none;
  }
}
