.head {
    width: 20%;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 35px;
}
h4 {
    margin-bottom: 10px;
}
.pil, .pip {
    margin-bottom: 7px;
}
.no {
    display: -webkit-flex;
    display: -webkit-box;
    display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
    align-items: center;
    margin-bottom: 30px;
}
.verify, .verification {
    text-decoration: none;
    background-color: yellow;
    text-align: center;
    padding: 10px;
    color: white;
    
}
select {
    margin-left: 10px;
    border-radius: 10px;
    border: none;
    padding: 10px;
    margin-bottom: 4px;
    border: none;
    background-color: #f0f0f0;
    color: gray;
    margin-right: 10px;
}
option {
    border-radius: 10px;
    border: none;
    padding: 10px;
    margin-bottom: 4px;
    border: none;
    background-color: #f0f0f0;
    color: gray;
}
.phone {
   width: 70%;
    border-radius: 10px;
    border: none;
    padding: 10px;
    margin-bottom: 4px;
    border: none;
    background-color: #f0f0f0;
    color: gray;
}